@font-face {
    font-family: 'Cera Pro';
    src: local('Cera Pro'), local('CeraPro-Regular'),
    url('CeraPro-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Cera Pro';
    src: local('Cera Pro Bold'), local('CeraPro-Bold'),
    url('CeraPro-Bold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
}